.curly {
    stroke-dasharray: 1427px;
    stroke: black;
    stroke-width: 10px;
    stroke-linejoin: round;
    stroke-linecap: round;
    stroke-dashoffset: 1427px;
    transition: stroke-dashoffset .5s;

    &.dash-in {
        stroke-dashoffset: 0;
    }
}

.flip-x {
    transform: scaleX(-1) !important;
}
